<template>
  <div
    style="
      background: rgba(255, 255, 255, 0.9);
      padding: 2vh 0 0 0;
      height: 86vh;
      margin: 2vh 10vw;
    "
  >
    <div style="text-align: center">
      选择工厂：
      <el-select v-model="factory" placeholder="请选择" @change="searchData()">
        <el-option
          v-for="item in factoryList"
          :key="item.value"
          :label="item.label"
          :value="item.value"
        >
        </el-option>
      </el-select>
      选择日期：
      <el-date-picker
        format="yyyy 年 MM 月 dd 日"
        value-format="yyyy-MM-dd"
        v-model="date"
        type="date"
        placeholder="选择日期"
        @change="searchData()"
      >
      </el-date-picker>
    </div>
    <table>
      <thead>
        <tr>
          <th>序号</th>
          <th>货号</th>
          <th>品名</th>
          <th>本月订单分配量</th>
          <th>今日产量</th>
          <th>今月未生产</th>
          <th>当月库存</th>
          <th>当月已发货</th>
          <th>标准日产量</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(item, index) in tableData" :key="index">
          <td>{{ index + 1 }}</td>
          <td>{{ item.product_code }}</td>
          <td>{{ item.product_name }}</td>
          <td>{{ !item.distribution_num ? 0 : item.distribution_num }}</td>
          <td>{{ !item.produce_num ? 0 : item.produce_num }}</td>
          <td>{{ !item.no_produce ? 0 : item.no_produce }}</td>
          <td>{{ !item.stock_num ? 0 : item.stock_num }}</td>
          <td>{{ !item.sum_delivery ? 0 : item.sum_delivery }}</td>
          <td>{{ !item.avg_delivery ? 0 : item.avg_delivery }}</td>
        </tr>
        <tr>
          <td>合计</td>
          <td></td>
          <td></td>
          <td>{{ tableAllNum[0] }}</td>
          <td>{{ tableAllNum[1] }}</td>
          <td>{{ tableAllNum[2] }}</td>
          <td>{{ tableAllNum[3] }}</td>
          <td>{{ tableAllNum[4] }}</td>
          <td>{{ tableAllNum[5] }}</td>
        </tr>
      </tbody>
    </table>
    <el-pagination
      background
      @current-change="handleCurrentChange"
      :current-page="page"
      layout="prev, pager, next, jumper"
      :total="total"
      style="margin: 15px 37%"
    ></el-pagination>
  </div>
</template>

<script>
import axios from "axios";
export default {
  data() {
    return {
      factory: "合迪", //工厂
      factoryList: [], //工厂下拉框
      date: "", //日期

      page: 1, //页数
      total: 0, //条数

      tableData: [], //表格数据
      tableAllNum: [], //表格最后一行合计
    };
  },
  methods: {
    getTableData() {
      axios({
        url: this._url2 + "server/stock_management/daily_inventory/",
        method: "get",
        params: {
          page: this.page,
          date: this.date,
          factory: this.factory,
        },
      }).then((res) => {
        console.log(res);
        this.tableData = res.data.data;
        this.total = res.data.count_data[0].count_num / 5;
        let distribution_numAll = 0;
        let produce_numAll = 0;
        let no_produceAll = 0;
        let stock_numAll = 0;
        let sum_deliveryAll = 0;
        let avg_deliveryAll = 0;
        for (let i = 0; i < this.tableData.length; i++) {
          distribution_numAll += Number(this.tableData[i].distribution_num);
          produce_numAll += Number(this.tableData[i].produce_num);
          no_produceAll += Number(this.tableData[i].no_produce);
          stock_numAll += Number(this.tableData[i].stock_num);
          sum_deliveryAll += Number(this.tableData[i].sum_delivery);
          avg_deliveryAll += Number(this.tableData[i].avg_delivery);
        }
        this.tableAllNum[0] = distribution_numAll;
        this.tableAllNum[1] = produce_numAll;
        this.tableAllNum[2] = no_produceAll;
        this.tableAllNum[3] = stock_numAll;
        this.tableAllNum[4] = sum_deliveryAll;
        this.tableAllNum[5] = avg_deliveryAll;
        // console.log(avg_deliveryAll);
      });
    },
    searchData() {
      this.page = 1;
      this.getTableData();
    },
    handleCurrentChange(newPage) {
      //分页器
      this.page = newPage;
      this.getTableData();
    },
  },
  created() {
    let date = new Date();
    let seperator1 = "-";
    let year = date.getFullYear();
    let month = date.getMonth() + 1;
    let strDate = date.getDate();
    if (month >= 1 && month <= 9) {
      month = "0" + month;
    }
    if (strDate >= 0 && strDate <= 9) {
      strDate = "0" + strDate;
    }
    let currentdate = year + seperator1 + month + seperator1 + strDate;
    this.date = currentdate;
    this.getTableData();
    //获取下拉框
    axios({
      url:
        this._url2 + "server/stock_management/get_order_distribution_supplier/",
      method: "get",
    }).then((res) => {
      console.log(res);
      for (let i = 0; i < res.data.data.length; i++) {
        let obj = {
          label: res.data.data[i][0],
          value: res.data.data[i][0],
        };
        this.factoryList.push(obj);
      }
    });
  },
};
</script>

<style scoped>
table {
  /*width: 84vw;*/
  margin: 10px auto;
  border-collapse: collapse;
}

tbody {
  display: block;
  max-height: 70vh;
  width: fit-content;
  overflow-y: auto;
  border-bottom: 1px #e5e5e5 solid;
}

thead,
tbody tr {
  display: table;
  width: fit-content;
  table-layout: fixed;
}

/*th和td的宽度使用vw或者px*/
thead th,
tbody tr td {
  border: 1px solid #e5e5e5;
  border-top: none;
  text-align: center;
  width: 6vw;
}

table thead tr {
  border-top: 1px solid #e5e5e5;
  background-color: #f7f5f5;
}

table > tbody > tr:hover {
  background: #eaf7e0;
}

table > thead > tr > th {
  font-size: 0.9rem;
  color: #000;
  font-weight: 600;
  height: 2.5vh;
  line-height: 2.5vh;
}

table > tbody > tr > td {
  font-size: 0.7rem;
  color: #000;
  height: 2.5vh;
  line-height: 2.5vh;
}

/* 设置滚动条的大小 */
::-webkit-scrollbar {
  width: 9px;
  height: 9px;
}

::-webkit-scrollbar-thumb {
  border-radius: 5px;
  background-color: #dddddd;
  background-clip: padding-box;
  min-height: 5vh;
}

::-webkit-scrollbar-thumb:hover {
  background-color: #bbb;
}

table tr th:nth-child(1),
table tr td:nth-child(1) {
  width: 3vw;
}
table tr th:nth-child(3),
table tr td:nth-child(3),
table tr th:nth-child(4),
table tr td:nth-child(4) {
  width: 13vw;
}
table tr th:nth-child(5),
table tr td:nth-child(5),
table tr th:nth-child(6),
table tr td:nth-child(6),
table tr th:nth-child(7),
table tr td:nth-child(7),
table tr th:nth-child(8),
table tr td:nth-child(8),
table tr th:nth-child(9),
table tr td:nth-child(9),
table tr th:nth-child(2),
table tr td:nth-child(2) {
  width: 8vw;
}
</style>


